  z-index: 3;
  background-color: #f6a603;
  border-radius: 5vmin;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__seconds-hand {
  animation-duration: 10s;
}

.stopwatch__minutes-hand {
  animation-duration: 10min;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
